*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    &::after,&::before{
        box-sizing: border-box;
    }
}
ul{
    margin: 0;
}
.container{
    max-width: 1202px;
    padding: 0 15px;
    margin: 0 auto;
}
.button{
        font-size: 24px;
        font-family: Raleway;
        font-weight: 900;
        text-transform: uppercase;
        max-width: 370px;
        width: 100%;
        padding: 21px 16px;
        border:none; 
        color: #ffffff;
        background: #FF4343;;
        cursor: pointer;
        position: relative;
        &:hover{
            background:#E71414;
            transition: background 0.5s linear;
        }
        &:active{
            background: #FF4343;
            box-shadow: 4px 4px 30px 0px #FF4343;
        }
}
.nav-link{
    padding:33px 0 40px;
    text-decoration: none;
    color: #FFF;
    font-size: 13px;
    font-family: Raleway;
    //font-weight: 700;
    //text-transform: uppercase;
    //display:block;
    //position: relative;
       
    &:hover{
        color: #babbc5;
    }

    &:active, &:focus{
        color:#E71414
    }

    &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 100%; 
        width: 0;
        height: 3px;
        background-color: #babbc5;
        transition: width 0.3s ease; 
    }
    &:hover::before{
        width: 100%; 
        left: 0; 
    }

    &:active::before{
        background-color:#E71414
    }
        
}
.nav-item{
    min-width: 134px;
    //text-align: center;
    color:#ffffff;
    @media(max-width:1024px){
        min-width: 112px;
    }
}
.header{
   // width: 100%;
    top: 0;
    left: 0;
 //   position: fixed;
 //   z-index: 2

    &__container{
        position:relative;
        &::before{
            content: '';
            position: absolute;
            display: block;
            border-bottom: 1px solid #BABBC5;
            width: calc(100% - 30px);
            left:15px;
            top:100%;
            @media(max-width:767px){
                border: none;
            }
        }
        @media(max-width:767px){
            padding:22px 20px;
        }
    }
    &__menu{
        @media(max-width:1024px){
            box-sizing: border-box;
        }
        @media(max-width:767px){
            display: none;
        }
    }
    &__logo{
        display: flex;
        box-sizing: border-box;
    }
    &__burger {
        display: none;
        padding: 10px 2px;
        background-color: transparent;
        border: 0;
        outline: none;
        cursor: pointer;

        @media(max-width:767px){
            display: block;
        }
        &-item {
            width: 35px;
            height: 3px;
            display: block;
            position: relative;
            background-color: #fff;
            font-size: 0;
            color: transparent;
            
            &:after{
                content: "";
                width: 100%;
                height: 100%;
                background-color: #fff;
                position: absolute;
                left: 0;
                z-index: 1;
                top: 10px;
            }
            &:before{
                content: "";
                width: 100%;
                height: 100%;
                background-color: #fff;
                position: absolute;
                left: 0;
                z-index: 1;
                top: -10px;
            }
        }
    }
    .menu{
        display:flex;
        list-style: none;
    
        &__item{
            min-width: 134px;
            text-align: center;
            color:#ffffff;
            @media(max-width:1024px){
                min-width: 112px;
            }
        }
        &__link{
            padding:44px 0 40px;
            text-decoration: none;
            color: #FFF;
            font-size: 13px;
            font-family: Raleway;
            font-weight: 700;
            text-transform: uppercase;
            display:block;
           
            &:active{
                color:#E71414
            }
    
            position: relative;
            &::before {
                content: "";
                position: absolute;
                bottom: 0;
                left: 100%; 
                width: 0;
                height: 3px;
                background-color: #babbc5;
                transition: width 0.3s ease; 
            }
            
            &:hover::before{
                width: 100%; 
                left: 0; 
            }
    
            &:active::before{
                background-color:#E71414
            }
        }
    }
}
.banner{
    background: url(../images/header-background.jpg) center no-repeat;
    background-size: cover;
    padding-top: 100px;
    padding: 228px 0 124px;
    margin-bottom: 113px;
    @media(max-width:767px){
        padding: 130px 0 30px;
        min-height: 100vh;
        display: grid;
        align-items:center
    }

    &__container{
        text-align: center;
    }
    &__title{
        color: #FFF;
        text-align: center;
        font-size: 64px;
        font-family: Raleway;
        font-weight: 900;
        text-transform: uppercase;
        margin-bottom: 30px;
        position: relative;
        @media(max-width:1024px){
            font-size: 48px;
        }
        @media(max-width:767px){
            font-size: 36px;
            margin-bottom: 32px;
        }    
    }
    &__text{
        color: #FFF;
        font-size: 24px;
        font-family: Raleway;
        line-height: normal;
        max-width: 1010px;
        margin: 0 auto 80px;
        position: relative;
        @media(max-width:1024px){
            font-size: 18px;
        }
        @media(max-width:767px){
            font-size: 14px;
            margin-bottom: 50px;
        }       
    }
    &__button{
        position: relative;
        &:hover{
            background:#E71414;
            animation-name: bannerAnimation;
            animation-duration: 1s;
            animation-timing-function: ease-in-out;
        }
        &:active{
            background: #FF4343;
            box-shadow: 4px 4px 30px 0px #FF4343;
        }
        @keyframes bannerAnimation {
            from {
              transform: scale(1);
            }
            to {
              transform: scale(1.2);
            }
            100% {
                transform: scale(1.2);
            }
          }
        @media(max-width:1024px){
            max-width: 314px;
        }
        @media(max-width:767px){
            max-width: 100%;
        } 
    }
}
.features{
    margin-bottom: 114px;
    @media(max-width:1024px){
        margin-bottom: 80px;
    }
    @media(max-width:767px){
        margin-bottom: 35px;
    }
    &__container{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        column-gap: 30px;
        row-gap: 71px;
        @media(max-width:1024px){
            column-gap: 20px;
            row-gap: 50px;
        }
        @media(max-width:767px){
            grid-template-columns: 1fr;
        }
    }
    &__item{
        text-align: center;
    }
    &__icon-box{
        justify-content: center;
        margin-bottom: 49px;
    }
    &__icon{
        @media(max-width:1024px){
            max-width: 85px;
            width:100%;
        }
    }
    &__title{
        color: #2D3033;
        font-size: 24px;
        font-family: Raleway;
        font-style: normal;
        font-weight: 900;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 47px;
        @media(max-width:1024px){
            font-size: 18px;
        }
    }
    &__text{
        color: #6C7279;
        text-align: center;
        font-size: 18px;
        font-family: Raleway;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        @media(max-width:1024px){
            font-size: 14px;
        }
    }
}
.work{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    margin-bottom: 114px;
    gap: 2px;
    @media (max-width:767px) {
        grid-template-columns: repeat(2,1fr);     
    }
    &__picture{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__item{
        position: relative;
        aspect-ratio: 1/0.9;
        overflow: hidden;
        &:hover .content{
            display: flex;
            flex-direction: column;
            row-gap: 20px;
            background-color: #FF4343CC;
        }
        &:active .content{
            display: flex;
            flex-direction: column;
            row-gap: 20px;
            background-color: #B90C0CCC;
        }
    }
    .content{
        justify-content: center;
        display:none;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2;
        &__title{
            color: #FFF;
            text-align: center;
            font-size: 24px;
            font-family: Raleway;
            font-style: normal;
            font-weight: 900;
            line-height: normal;
            text-transform: uppercase;
            @media(max-width:1024px){
                font-size: 18px;
            }
            @media(max-width:767px){
                padding:18px 0;
            }
        }
        &__text{
            color: #FFF;
            text-align: center;
            font-size: 18px;
            font-family: Raleway;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            @media(max-width:1024px){
                font-size: 14px;
            }
            @media(max-width:767px){
                min-width: 100%;
                padding:0
            }
        }
    }
}
.team{
    margin-bottom: 114px;
    @media (max-width:767px) {
        margin-bottom: 35px;
    }
    &__container{
        display: grid;
        grid-template-columns: repeat(4,1fr);  
        column-gap: 30px;
        @media (max-width:767px) {
            grid-template-columns: repeat(1,1fr);
        }
    }
    &__item{
        display: flex;
        flex-direction: column;
        @media (max-width:767px) {
            margin-bottom: 50px;
        }
    }
    &__item:last-child{
        @media (max-width:767px) {
            margin-bottom: 0;
        }
    }
    &__img-wrap{
        display: block;
        margin-bottom: 30px;
        aspect-ratio: 1;
    }
    &__img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__name{
        color: #2D3033;
        font-family: Raleway;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: capitalize;
        margin-bottom: 5px;
        @media (max-width:767px) {
          font-size: 18px;
        }
    }
    &__place{
        color: #FF4343;
        font-family: Raleway;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 18px;
        @media (max-width:767px) {
            font-size: 14px;
        }
    }
    &__text{
        color: #6C7279;
        font-family: Raleway;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 38px;
        @media (max-width:767px) {
            font-size: 14px;
        }
    }
    .social{
        display: flex;
        column-gap: 6px;
        margin-top: auto;
        .social__link{
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid #C6CBCD;

            &:hover{
                border: 1px solid #a39d9d;
            }
            &:active{
                box-shadow: 1px 1px 10px 0px #6e6b6b;
            }
        }
    }
}

.download{
    text-align: center;
    margin-bottom: 114px;
    &__title{
        font-family: Raleway;
        font-size: 30px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin-bottom: 22px;
        @media(max-width:1024px){
            margin-bottom: 17px;
            font-size: 24px;
        }
    }
    &__text{
        font-family: Raleway;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        margin-bottom: 48px;
        @media(max-width:1024px){
            margin-bottom: 44px;
            font-size: 14px;
        }
    }
    &__button{
        @media(max-width:767px){
            max-width: 100%;
        }
        animation-name: downloadAnimation;
        animation-duration: 1s;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;
    }

    @keyframes downloadAnimation {
        0% {
          transform: translateY(0);
        }
        50% {
          transform: translateY(-10px);
        }
        100% {
          transform: translateY(0);
        }
      }
}
.footer{
    background: #353C65;
    &__container{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        text-align: center;
        padding: 71px 15px 55px;

        @media(max-width:767px){
            display: flex;
            flex-direction: column;
            row-gap: 65px;
        }
    }
    &__title{
        margin-bottom: 29px;
        color: #FFF;
        text-align: center;
        font-family: Raleway;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        @media(max-width:1024px){
            font-size: 14px;
        }
    }
    &__text{
        color: #FFF;
        text-align: center;
        font-family: Raleway;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-transform: capitalize;
        @media(max-width:1024px){
            font-size: 14px;
        }
    }
    &__social{
        display: flex;
        column-gap: 23px;
        justify-content: center;
        margin-top: 48px;
    }
    &__link{
        width: 33px;
        height: 33px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #C6CBCD;
        &:hover{
            border: 1px solid #eee7e7;
        }
        &:active{
            box-shadow: 1px 1px 10px 0px #ffffff;
        }
    }

    &__copy{
        background: #2C3154;
        padding: 24px 0;
        &-text{
            color: #808080;
            text-align: center;
            font-family: Raleway;
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            text-transform: capitalize;
            @media(max-width:1024px){
                font-size: 14px;
            }

        }
        &-span{
            color:#FFF
        }
        &-box{
            max-width: 385px;
            margin: 0 auto;
            padding: 0 15px;
            @media(max-width:1024px){
                max-width: 314px;
            }
        }
    }
}

.swiper{
    .swiper-block{
        padding-bottom: 50px;
        margin-bottom: 141px;
    }
    .swiper-pagination{
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 15px;
    }
    .swiper-pagination-bullet{
        border: 1px solid #7E5D6F;
        background-color: transparent;
        opacity: 1;
        width: 14px;
        height: 14px;
    }
    .swiper-pagination-bullet-active{
        background-color: #7E5D6F;
    }
}
.swiper-block{
    padding-bottom: 50px;
    margin-bottom: 141px;
}
.swiper-pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 15px;
}
.swiper-pagination-bullet{
    border: 1px solid #7E5D6F;
    background-color: transparent;
    opacity: 1;
    width: 14px;
    height: 14px;
}
.swiper-pagination-bullet-active{
    background-color: #7E5D6F;
}
.slider{
    display: grid;
    grid-template-columns: 51% 49%;
    @media(max-width:767px){
        display: block;
    }

    &__wrap-image{
        position: relative;
        @media(max-width:767px){
            aspect-ratio: 1/0.7;
        }
    }

    &__image{
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        left: 0;
        top: 0;
    }

    &__content{
        background-color: #5C3249;
        display: grid;
        justify-content: center;
        padding: 116px 16px;
        @media(max-width:767px){
            display: block;
            padding:  27px 15px 95px ;
            text-align: center;
        }
    }

    &__title{
        color: #FFF;
        font-family: Cardo;
        font-size: 36px;
        font-style: italic;
        font-weight: 400;
        line-height: normal;
        text-transform: capitalize;
        max-width: 471px;
        align-self: center;
        @media(max-width:1024px){
            max-width: 330px;
            font-size: 24px;
            margin-bottom: 17px;
        }
        @media(max-width:767px){
            max-width: 100%;
        }
        
    }
    &__text{
        color: #FFF;
        font-family: Raleway;
        font-size: 13px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        @media(max-width:1024px){
            font-size: 12px;
        }
        @media(max-width:767px){
            max-width: 100%;
        }
    }
}
.show{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    padding:107px 15px;
    background: url(../images/header-background.jpg) center no-repeat;
    z-index: -1;
}